let echarts = require('echarts/lib/echarts')

export default {
  name: "radar-chart",
  props:["name"],
  data() {
    return {
      myChart: {},
    }
  },
  mounted() {
    let this_ = this;
    this.myChart = echarts.init(document.getElementById('myChart'));
    let option = {
      // backgroundColor:'black',
      "normal": {
        "top": 200,
        "left": 300,
        "width": 500,
        "height": 400,
        "zIndex": 6,
        "backgroundColor": ""
      },
      "color": ["rgba(245, 166, 35, 1)", "rgba(19, 173, 255, 1)"],
      "title": {
        "show": true,
        "text": "",
        "left": "40%",
        "top": "1%",
        "textStyle": {
          "fontSize": 18,
          "color": "#fff",
          "fontStyle": "normal",
          "fontWeight": "normal"
        }
      },
      "tooltip": {
        "show": true,
        "trigger": "item"
      },
      "legend": {
        "show": true,
        "icon": "circle",
        "left": "35%",
        "top": "90%",
        "orient": "horizontal",
        "textStyle": {
          "fontSize": 14,
          "color": "#fff"
        },
        "data": ["1", "2"]
      },
      "radar": {
        "center": ["50%", "50%"],
        "radius": "70%",
        "startAngle": 90,
        "splitNumber": 4,
        "shape": "circle",
        "splitArea": {
          "areaStyle": {
            "color": ["transparent"]
          }
        },
        "axisLabel": {
          "show": false,
          "fontSize": 18,
          "color": "#fff",
          "fontStyle": "normal",
          "fontWeight": "normal"
        },
        "axisLine": {
          "show": true,
          "lineStyle": {
            "color": "grey"//
          }
        },
        "splitLine": {
          "show": true,
          "lineStyle": {
            "color": "grey"//
          }
        },
        "indicator": [{
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }, {
          "name": "",
          "max": 88
        }]
      },
      "series": [{
        "name": "1",
        "type": "radar",
        "symbol": "circle",
        "symbolSize": 10,
        "areaStyle": {
          "normal": {
            "color": "rgba(245, 166, 35, 0.4)"
          }
        },
        itemStyle: {
          color: 'rgba(245, 166, 35, 1)',
          borderColor: 'rgba(245, 166, 35, 0.3)',
          borderWidth: 10,
        },
        "lineStyle": {
          "normal": {
            "type": "dashed",

            "color": "rgba(245, 166, 35, 1)",
            "width": 2
          }
        },
        "data": [
          [80, 50, 55, 80, 50, 80, 48, 43, 60, 78, 60, 40, 42, 44, 65]
        ]
      }, {
        "name": "2",
        "type": "radar",
        "symbol": "circle",
        "symbolSize": 10,
        "itemStyle": {
          "normal": {
            color: 'rgba(19, 173, 255, 1)',
            "borderColor": "rgba(19, 173, 255, 0.4)",
            "borderWidth": 10
          }
        },
        "areaStyle": {
          "normal": {
            "color": "rgba(19, 173, 255, 0.5)"
          }
        },
        "lineStyle": {
          "normal": {
            "color": "rgba(19, 173, 255, 1)",
            "width": 2,
            "type": "dashed"
          }
        },
        "data": [
          [60, 60, 65, 60, 70, 40, 80, 63, 68, 60, 77, 60, 80, 62, 80]
        ]
      }]
    };
    this.myChart.setOption(option);
    // setTimeout(() => {
    //   console.log('21212122222')
    //   window.addEventListener('resize',function() {myChart.resize()});
    // }, 10)
  },
  methods: {
    test() {
      console.log('diaxxxxxxxxxx')
      this.myChart.resize()
    }
  },
  watch:{
    name:function(ffff ,bbbb){
      console.log('*************')
      let that = this
      setTimeout(function () {
        that.myChart.resize()
      },500)

      // this.$nextTick(()=>{
      //   this.myChart.resize()
      // })
    }
  },
}



